import React, { Component } from 'react';
import './css/App.css';
import styled from 'styled-components';
import Header from './components/Header';
import Navbar from './components/Navbar';
import About from './components/About';
import Projects from './components/Projects';
import Contact from './components/Contact';

const MainContainer = styled.div`
  text-align: center;
  color: blue;
  font-size: 3rem;
  background-color: palevioletred;
`;

const SomeText = styled.p`
  text-transform: uppercase;
  color: red;
  text-align: left;
`;

class App extends Component {

  constructor(props) {
    super(props);
    this.state = { 
      navPosition: "static",
      aboutHeight: "60rem",
      aboutPadding: "10rem 5rem 10rem 5rem"
    };

    this.handleScroll = this.handleScroll.bind(this);
  }

  handleScroll(navOffset) {
    if (window.pageYOffset >= navOffset) {
      this.setState({ 
        navPosition: "fixed",
        aboutHeight: "65rem",
        aboutPadding: "15rem 5rem 10rem 5rem"
      });
    } else {
      this.setState({ 
        navPosition: "static",
        aboutHeight: "60rem",
        aboutPadding: "10rem 5rem 10rem 5rem"
      });
    }
  }

  render() {
    return (
      <MainContainer>
        <Header />
        <Navbar handleScroll={this.handleScroll} navPosition={this.state.navPosition} />
        <About aboutHeight={this.state.aboutHeight} aboutPadding={this.state.aboutPadding} />
        <Projects />
        <Contact />
      </MainContainer>
    );
  }
}

export default App;